  --primary: #FCD535;
  --bg: #0f1015;
  --rad: .7rem;
  --dur: .3s;
  --color-dark: #2f2f2f;
  --color-light: #fff;
  --color-brand: #FCD535;
  --font-fam: 'Lato', sans-serif;
  --height: 2.8rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, 0.43, 1.49);
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
  scroll-behavior: smooth;
}

body{
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
  background-color: #f4f4f4;
  color: black;
  display: flex;
  flex-direction: column;
}
.skip-link {
  position: absolute;
  height: 44px;
  width: 150px;
  top: -48px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}
 
.skip-link:focus {
   top: 90px;
}
a:focus {
  outline: 6px solid rgb(113, 19, 19);
}

button:focus {
  outline: 3px solid rgb(113, 19, 19);
}

input:focus {
  outline: 6px solid rgb(113, 19, 19);
}

a{
  min-width: 44px !important;
  min-height: 44px !important;
}
/* navbar */
h1 {
  font-size: 20px;
}
header .navbar{
  height: 75px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: var(--bg);
  color: var(--primary);
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10000;
}
.navbar-logo{
  color: var(--primary);
  font-size: 20px;
  width: 100px;
  height: 50px;
  margin-top: 27px;
}
 
.navbar .navbar-nav{
  margin-right: 20px;
  display: flex;
  gap: 26px;
  height: 40px;
}
.navbar .navbar-nav a{
  color: var(--primary);
  padding: 10px;
  font-size: 16px;
  height: 44px;
}

.navbar .navbar-nav a:hover{
  color: var(--primary);
}

.navbar .navbar-nav a::after{
  content: '';
  display: block;
  color: var(--primary);
  padding-bottom: 2px;
  transform: scaleX(0);
  transition: ease-in-out 0.2s;
}

.navbar .navbar-nav a:hover::after{
  border-bottom: 2px solid var(--primary);
  transform: scaleX(0.7);
}

#humberger{
  display: none;
}

main {
  padding: 0;
  flex: 1;
}

/* Hero */
