und-color: #bf1722;\n  color: white;\n  padding: 8px;\n  z-index: 100;\n}\n \n.skip-link:focus {\n   top: 90px;\n}\na:focus {\n  outline: 6px solid rgb(113, 19, 19);\n}\n\nbutton:focus {\n  outline: 3px solid rgb(113, 19, 19);\n}\n\ninput:focus {\n  outline: 6px solid rgb(113, 19, 19);\n}\n\na{\n  min-width: 44px !important;\n  min-height: 44px !important;\n}\n/* navbar */\nh1 {\n  font-size: 20px;\n}\nheader .navbar{\n  height: 75px;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 20px;\n  background-color: var(--bg);\n  color: var(--primary);\n  position: fixed;\n  width: 100%;\n  top: 0;\n  left: 0;\n  right: 0;\n  z-index: 10000;\n}\n.navbar-logo{\n  color: var(--primary);\n  font-size: 20px;\n  width: 100px;\n  height: 50px;\n  margin-top: 27px;\n}\n \n.navbar .navbar-nav{\n  margin-right: 20px;\n  display: flex;\n  gap: 26px;\n  height: 40px;\n}\n.navbar .navbar-nav a{\n  color: var(--primary);\n  padding: 10px;\n  font-size: 16px;\n  height: 44px;\n}\n\n.navbar .navbar-nav a:hover{\n  color: var(--primary);\n}\n\n.navbar .navbar-nav a::after{\n  content: '';\n  display: block;\n  color: var(--primary);\n  padding-bottom: 2px;\n  transform: scaleX(0);\n  transition: ease-in-out 0.2s;\n}\n\n.navbar .navbar-nav a:hover::after{\n  border-bottom: 2px solid var(--primary);\n  transform: scaleX(0.7);\n}\n\n#humberger{\n  display: none;\n}\n\nmain {\n  padding: 0;\n  flex: 1;\n}\n\n/* Hero */\n/* #hero{\n  display: flex;\n  margin-top: 0;\n  height: 100vh;\n  width: 100%;\n  background: rgba(0,0,0,0.7)url(../public/images/heros/hero-image_4.jpg) no-repeat center /cover;\n  background-size: cover;\n  background-blend-mode: darken;\n} */\n\n#hero {\n  display: flex;\n  position: relative;\n  margin-top: 0;\n  width: 100%;\n  height: 100vh;\n  overflow: hidden;\n}\n.hero-image {\n  width: 100%;\n  height: auto;\n  object-fit: cover;\n  z-index: 1; \n  filter: brightness(50%);\n}\n\n.hero-text{\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  width: 100%;\n  z-index: 2;\n}\n\n.hero-text h2{\n  color: var(--primary);\n  font-size: 38px;\n}\n\n.hero-text p{\n  color: white;\n  font-size: 18px;\n  margin-top: 6px;  \n}\n.hero-text a{\n  color: var(--primary);\n  font-size: 18px;\n  margin-top: 60px;  \n  padding: 22px 30px 22px 0px;\n}\n.hero-text a:hover{\n  color: var(--primary);\n  font-weight: bold;\n  transition: 0.2s;\n}\n\n/* // search styles */\n.form-search {\n  margin-top: 20px;\n  position: relative;\n  width: 30rem;\n  background: var(--color-brand);\n  border-radius: var(--rad);\n}\ninput, .btn_search {\n  height: var(--height);\n  font-family: var(--font-fam);\n  border: 0;\n  color: var(--color-dark);\n  font-size: 1rem;\n}\ninput[type=\"search\"] {\n  outline: 0; \n  width: 100%;\n  background: var(--color-light);\n  padding: 0 1.6rem;\n  border-radius: var(--rad);\n  appearance: none; \n  transition: all var(--dur) var(--bez);\n  transition-property: width, border-radius;\n  z-index: 1;\n  position: relative;\n}\n.btn_search {\n  display: none;\n  position: absolute;\n  top: 0;\n  right: 0;\n  width: var(--btn-width);\n  font-weight: bold;\n  background: var(--color-brand);\n  border-radius: 0 var(--rad) var(--rad) 0;\n}\ninput:not(:placeholder-shown) {\n  border-radius: var(--rad) 0 0 var(--rad);\n  width: calc(100% - var(--btn-width));\n  + button {\n    display: block;\n  }\n}\n.label_search {\n  position: absolute;\n  clip: rect(1px, 1px, 1px, 1px);\n  padding: 0;\n  border: 0;\n  height: 1px;\n  width: 1px;\n  overflow: hidden;\n}\n /* end search */\n\n/*all resto text*/\n.all-resto h2{\n  margin-top: 20px;\n  text-align: center;\n  padding: 10px;  \n  background-color: #eeeeee;\n}\n\n/* footer */\nfooter{\n  margin-bottom: 0;\n  text-align: center;\n  width: 100%;\n  padding: 30px;\n  background-color:var(--bg);\n  bottom: 0;\n  right: 