___CSS_LOADER_EXPORT___.push([module.id, `/* media query*/
@media screen and (max-width: 1200px){
    body {
      font-size: 16px;
    }
  }
  
  @media screen and (max-width: 768px){
    body {
      font-size: 16px;
    }
    #hero {
      height: 50vh;
    }
    #humberger{
      font-size: 25px;
      padding: 10px;
      display: block;
      color: var(--primary);
      width: 50px;
      height: 50px;
    }
    .navbar .navbar-nav {
      display: none;
      position: absolute;
      top: 100%;
      right: -100%;
      margin-right: 0;
      background-color:#1e2129d8;
      width: 50%;
      height: 100vh;
    }
    .navbar .navbar-nav.active {
      display: block;
      right: 0;
    }
    .navbar .navbar-nav a {
      display: block;
      text-align: center;
      margin: 30px 20px;
      width: 90px;
      height: 44px;
    }
  
    .navbar .navbar-nav a:hover{
      color: #f8b851;
    }
    .navbar .navbar-nav a::after{
      content: '';
      display: block;
      color: var(--primary);
      border-bottom: 2px solid var(--primary);
      padding-bottom: 2px;
      transform: scaleX(0);
      transition: ease-in-out 0.2s
    }
    
    .navbar .navbar-nav a:hover::after{
      border-bottom: 2px solid var(--primary);
      transform: scaleX(0.2);
    }
  
  .hero-text a{
    color: var(--primary);
    font-size: 18px;
    width: 44px;
    height: 44px;
  }
  .hero-text a:hover{
    color: var(--primary);
    font-weight: bold;
    transition: 0.2s;
  }
  
    .cards{
      grid-template-columns: repeat(2, 1fr);
    }
    .card_image {
      height: calc(9*1.2rem);
      padding: 1.2rem 1.2rem 0;
      position:relative;
    }
    .review_item {
      padding: 0.7rem 0.7rem;
      max-width: 100%;
    }
  }
  
  @media screen and (max-width: 478px){
    body {
      font-size: 12px;
    }
    .cards {
        grid-template-columns:repeat(1, 1fr);
        margin: 5px 10px;
        padding: 1rem;
        /* box-shadow: 10px 10px 10px 10px #eee; */
        border-radius: 10px;
      }
    form {
        margin-top: 20px;
        position: relative;
        width: 15rem;
        background: var(--color-brand);
        border-radius: var(--rad);
    }
    .form-search {
      max-width: 15rem;
    }
    .card_item_detail {
      grid-template-columns:repeat(1, 1fr);
      gap: 40px;
    }
    }`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA,eAAe;AACf;IACI;MACE,eAAe;IACjB;EACF;;EAEA;IACE;MACE,eAAe;IACjB;IACA;MACE,YAAY;IACd;IACA;MACE,eAAe;MACf,aAAa;MACb,cAAc;MACd,qBAAqB;MACrB,WAAW;MACX,YAAY;IACd;IACA;MACE,aAAa;MACb,kBAAkB;MAClB,SAAS;MACT,YAAY;MACZ,eAAe;MACf,0BAA0B;MAC1B,UAAU;MACV,aAAa;IACf;IACA;MACE,cAAc;MACd,QAAQ;IACV;IACA;MACE,cAAc;MACd,kBAAkB;MAClB,iBAAiB;MACjB,WAAW;MACX,YAAY;IACd;;IAEA;MACE,cAAc;IAChB;IACA;MACE,WAAW;MACX,cAAc;MACd,qBAAqB;MACrB,uCAAuC;MACvC,mBAAmB;MACnB,oBAAoB;MACpB;IACF;;IAEA;MACE,uCAAuC;MACvC,sBAAsB;IACxB;;EAEF;IACE,qBAAqB;IACrB,eAAe;IACf,WAAW;IACX,YAAY;EACd;EACA;IACE,qBAAqB;IACrB,iBAAiB;IACjB,gBAAgB;EAClB;;IAEE;MACE,qCAAqC;IACvC;IACA;MACE,sBAAsB;MACtB,wBAAwB;MACxB,iBAAiB;IACnB;IACA;MACE,sBAAsB;MACtB,eAAe;IACjB;EACF;;EAEA;IACE;MACE,eAAe;IACjB;IACA;QACI,oCAAoC;QACpC,gBAAgB;QAChB,aAAa;QACb,0CAA0C;QAC1C,mBAAmB;MACrB;IACF;QACI,gBAAgB;QAChB,kBAAkB;QAClB,YAAY;QACZ,8BAA8B;QAC9B,yBAAyB;IAC7B;IACA;MACE,gBAAgB;IAClB;IACA;MACE,oCAAoC;MACpC,SAAS;IACX;IACA","sourcesContent":["/* media query*/\r\n@media screen and (max-width: 1200px){\r\n    body {\r\n      font-size: 16px;\r\n    }\r\n  }\r\n  \r\n  @media screen and (max-width: 768px){\r\n    body {\r\n      font-size: 16px;\r\n    }\r\n    #hero {\r\n      height: 50vh;\r\n    }\r\n    #humberger{\r\n      font-size: 25px;\r\n      padding: 10px;\r\n      display: block;\r\n      color: var(--primary);\r\n      width: 50px;\r\n      height: 50px;\r\n    }\r\n    .navbar .navbar-nav {\r\n      display: none;\r\n      position: absolute;\r\n      top: 100%;\r\n      right: -100%;\r\n      margin-right: 0;\r\n      background-color:#1e2129d8;\r\n      width: 50%;\r\n      height: 100vh;\r\n    }\r\n    .navbar .navbar-nav.active {\r\n      display: block;\r\n      right: 0;\r\n    }\r\n    .navbar .navbar-nav a {\r\n      display: block;\r\n      text-align: center;\r\n      margin: 30px 20px;\r\n      width: 90px;\r\n      height: 44px;\r\n   